import React from 'react'
import "./styles.css"
import 'bootstrap/dist/css/bootstrap.min.css';

export default function Desc() {
  return (
    <div className="description" id='firm'>
        <p>
          <hr />
          Pracownia zegarmistrzowska Henryka Trzoska powstała w 1963 roku w Warszawie i niezmiennie od początku istnienia związana jest z Mokotowem. Obecnie znajduje się przy ulicy Puławskiej 96, nieopodal zbiegu z ulicą Odyńca. Dziś trudno sobie wyobrazić, że jej tam nie ma - zrosła się trwale z tkanką południowej dzielnicy Warszawy. Charakterystyczny zegar, zdobiący front kamienicy nie tylko wskaże miejsce, w którym czas jest najważniejszy, ale pozwala nieśpiesznym przechodniom upewnić się, że "Tempus Fugit". Jednak przy Puławskiej 96 czas jakby się zatrzymał. Tutaj bowiem stare tradycyjne rzemiosło znakomicie broni się przed nowymi technologiami, jednocześnie - co zrozumiałe - ustępując im nieco miejsca. Mokotowska pracownia, to dwa pokolenia zegarmistrzów, którzy dbają o to co było dawniej i o to, co jest aktualne i modne dziś. Obecnie firma działa pod nazwą Trzosek Henryk i Syn. Od lat 90-tych panowie Henryk i Maciej dbają o zegary odmierzające czas w Pałacu Prezydenckim, Belwederze i Urzędzie Rady Ministrów. Aby duszpasterze mogli spełniać swoją misję we właściwym czasie, firma Trzosek Henryk i Syn dogląda mechanizmów w Archidiecezji Warszawskiej. Pod opieką konserwatorską znalazły się także zegary użyteczności publicznej: kościół w Lipnie, kościół w Puławach, a także zegar w jednym z warszawskich urzędów skarbowych. W portfolio Macieja znajdziemy doświadczenie zdobyte w La Boutique Swiss, Hermitage Boutique, Hublot, Swatch Group, Wrzeciono Czasu. Dla zainteresowanych nazwy te nie stanowią tajemnicy. Do pracowni panów Henryka i Macieja zawsze warto zajrzeć. Jest to miejsce wyjątkowe na usługowej mapie współczesnej Warszaw. Tradycja łączy się tu z nowoczesnością, a niepowtarzalny klimat i fachowa obsługa sprawiają, że na Puławską 96 chce się po prostu wracać.
          <hr />
        </p>
    </div>
  )
}
